
.toplevel_page_dialog-contact-form .wrap,
#dialog-contact-form * {
  box-sizing: border-box;
}

.toplevel_page_dialog-contact-form {
  box-sizing: border-box;
}
.toplevel_page_dialog-contact-form * {
    box-sizing: border-box;
}
.toplevel_page_dialog-contact-form .dcf-copy-shortcode {
    background-color: #f1f1f1;
    letter-spacing: 1px;
    padding: 5px 8px;
    width: 100%;
    max-width: 20em;
}
.toplevel_page_dialog-contact-form .dcf-copy-shortcode.widefat {
      max-width: 100%;
}
.toplevel_page_dialog-contact-form .svg-wrapper {
    padding: 5px;
    border: 1px solid #dddddd;
    display: inline-block;
    position: relative;
}
.toplevel_page_dialog-contact-form .svg-wrapper .tooltip-text {
      visibility: hidden;
      width: 140px;
      background-color: #555;
      color: #fff;
      text-align: center;
      border-radius: 6px;
      padding: 5px;
      position: absolute;
      z-index: 1;
      bottom: 150%;
      left: 50%;
      margin-left: -75px;
      opacity: 0;
      transition: opacity 0.3s;
}
.toplevel_page_dialog-contact-form .svg-wrapper .tooltip-text::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%;
        margin-left: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: #555 transparent transparent transparent;
}
.toplevel_page_dialog-contact-form .svg-wrapper:hover .tooltip-text {
      visibility: visible;
      opacity: 1;
}
.toplevel_page_dialog-contact-form .svg-wrapper svg {
      overflow: hidden;
      display: block;
}
@media screen and (min-width: 783px) {
.toplevel_page_dialog-contact-form .wp-list-table th.title, .toplevel_page_dialog-contact-form .wp-list-table td.title {
      width: calc(50% - 2.5em);
}
.toplevel_page_dialog-contact-form .wp-list-table th.shortcode, .toplevel_page_dialog-contact-form .wp-list-table td.shortcode {
      width: 40%;
}
.toplevel_page_dialog-contact-form .wp-list-table th.entries, .toplevel_page_dialog-contact-form .wp-list-table td.entries {
      width: 10%;
}
}














































.table-loading {
  position: relative;
}
.table-loading .table-loader-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9;
}
.table-loading .table-loader-wrap .table-loader-center {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
}
.table-loading .wp-list-table,
  .table-loading .tablenav {
    opacity: 0.4;
}
.table-loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #ffffff;
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  animation: tableLoading 1s infinite linear;
  transform: translateZ(0);
}
.table-loader:before {
    width: 50%;
    height: 50%;
    background: #ffffff;
    border-radius: 100% 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    content: '';
}
.table-loader:after {
    background: #f4f4f4;
    width: 75%;
    height: 75%;
    border-radius: 50%;
    content: '';
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
@-webkit-keyframes tableLoading {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
@keyframes tableLoading {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}








































































































































































